// 初始化
$(function() {
  initCategoryActive()
  initContentShow()
  onCategoryActive()
})

// 变量
var activeCategory = location.hash || $($('.faq__category-item')[0]).attr('href')

// 初始化类别active状态
function initCategoryActive() {
  resetCategoryActive()
  $('.faq__category-item').each(function() {
    if($(this).attr('href') === activeCategory) {
      $(this).addClass('active')
    }
  })
}

// 重置类别active状态
function resetCategoryActive() {
  $('.faq__category-item').removeClass('active')
}

// 初始化类型展示状态
function initContentShow() {
  resetContentShow()
  $(changeIdSelectorToClassSelector(activeCategory)).show()
}

// 重置内容展示状态
function resetContentShow() {
  $('.faq__content').hide()
}

// 兼容类别active状态
function onCategoryActive() {
  $('.faq__category-item').click(function() {
    resetCategoryActive()
    $(this).addClass('active')
    resetContentShow()
    $(changeIdSelectorToClassSelector($(this).attr('href'))).show()
  })
}
